import Breadcrumb from '../components/common/Breadcrumb.vue'
import config from '../config/config'

export default {
    layout: 'social',
    components: { Breadcrumb },
    head: {
        title: 'Site Đại Lý' + config.siteName,
            htmlAttrs: {
            lang: 'en'
        },
        meta: [
            { charset: 'utf-8' },
            { name: 'viewport', content: 'width=device-width,initial-scale=1.0,user-scalable=0,minimal-ui' },
            { hid: 'description', name: 'description', content: config.description },
            { hid: 'og:description', name: 'og:description', content: config.description },
            { hid: 'keywords', name: 'keywords', content: config.keyword },
            { hid: 'dc.keywords', name: 'dc.keywords', content: config.keyword },
            { hid: 'news_keywords', name: 'news_keywords', content: config.keyword },
            { hid: 'og:site_name', name: 'og:site_name', content: config.siteName2 },
            { hid: 'dc.title', name: 'dc.title', content: config.title },
            { hid: 'og:title', name: 'og:title', content: config.title },
            { name: 'format-detection', content: 'telephone=no' },
        ],
    },
    data() {
        return {
            breadcrumbs: {
                title: 'Site đại lý',
                items: [
                    'Trang chủ',
                    'Site đại lý',
                ],
                links: [
                    '/home',
                    '/tich-hop-api',
                ]
            },
            user: this.$store.state.user,
        }
    },
    methods: {
        async copy (string) {
            let self = this;
            navigator.clipboard.writeText(string).then(function () {
                self.$notify.success({
                    title: 'success',
                    message: "Copy thành công " + string
                });
                
            }, function (err) {
                self.$notify.error({
                    title: 'Error',
                    message: err
                });
            });
        }
    },
    watch: {
        '$store.state.user': function() {
            this.user = this.$store.state.user
        }
    },
    mounted() {
        
    },
}
